export const headersTable = [
    { key: "firstPublishedAt", title: "Date", type: "date", width: "12ch" },
    {
        key: "amountTot",
        title: "$ Tot",
        type: "cents",
        width: "10ch",
        align: "end",
    },
    {
        key: "amountMonth",
        title: "$ Month",
        type: "cents",
        width: "10ch",
        align: "end",
    },
    { key: "title", title: "Title", type: "text" },
    {
        key: "wordCount",
        title: "Words",
        type: "numeric",
        width: "6ch",
        align: "end",
    },
];
export const ordersTable = [
    {
        key: "firstPublishedAt",
        functionOrderASC: (key, list) => [
            ...orderDatesASC(key, list),
        ],
        functionOrderDESC: (key, list) => [
            ...orderDatesDESC(key, list),
        ],
    },
    {
        key: "amountTot",
        functionOrderASC: (key, list) => [
            ...orderNumbersASC(key, list),
        ],
        functionOrderDESC: (key, list) => [
            ...orderNumbersDESC(key, list),
        ],
    },
    {
        key: "amountMonth",
        functionOrderASC: (key, list) => [
            ...orderNumbersASC(key, list),
        ],
        functionOrderDESC: (key, list) => [
            ...orderNumbersDESC(key, list),
        ],
    },
    {
        key: "title",
        functionOrderASC: (key, list) => [
            ...orderStringsASC(key, list),
        ],
        functionOrderDESC: (key, list) => [
            ...orderStringsDESC(key, list),
        ],
    },
    {
        key: "wordCount",
        functionOrderASC: (key, list) => [
            ...orderNumbersASC(key, list),
        ],
        functionOrderDESC: (key, list) => [
            ...orderNumbersDESC(key, list),
        ],
    },
];
export const chartsTable = ["amountTot", "amountMonth", "wordCount"];
export const calculateTotalsTable = (listStories, headersTable) => {
    const result = headersTable.map((header) => {
        let value = "";
        if (header.type === "numeric" || header.type === "cents") {
            value = calculateTotalKey(listStories, header.key);
        }
        return { ...header, value };
    });
    return result;
};
function calculateTotalKey(listStories, key) {
    return listStories.reduce((previous, current) => {
        return previous + current[key];
    }, 0);
}
function orderStringsASC(key, list) {
    return list.sort((a, b) => {
        const x = a[key].toUpperCase();
        const y = b[key].toUpperCase();
        return x > y ? 1 : x < y ? -1 : 0;
    });
}
function orderStringsDESC(key, list) {
    return list.sort((a, b) => {
        const x = a[key].toUpperCase();
        const y = b[key].toUpperCase();
        return x > y ? -1 : x < y ? 1 : 0;
    });
}
function orderNumbersASC(key, list) {
    return list.sort((a, b) => {
        const x = a[key];
        const y = b[key];
        return x > y ? 1 : x < y ? -1 : 0;
    });
}
function orderNumbersDESC(key, list) {
    return list.sort((a, b) => {
        const x = a[key];
        const y = b[key];
        return x > y ? -1 : x < y ? 1 : 0;
    });
}
function orderDatesASC(key, list) {
    return list.sort((a, b) => {
        const x = a[key].timestamp;
        const y = b[key].timestamp;
        return x > y ? 1 : x < y ? -1 : 0;
    });
}
function orderDatesDESC(key, list) {
    return list.sort((a, b) => {
        const x = a[key].timestamp;
        const y = b[key].timestamp;
        return x > y ? -1 : x < y ? 1 : 0;
    });
}
